.header{

    min-height: 60px;
    position: fixed;
    display: flex;
    align-items: center;
    padding-inline: 20px;
    z-index:5;
    justify-content: space-between;
    width: 100%;
}
.allModels{
    display: flex;
    gap: 20px;
    width: 60%;
    justify-content: center;
}
.model a{
    text-transform: uppercase;
    text-decoration: none;
    font-size: 1.1rem;
    font-weight: 600;
}
.rightNav{
    display: flex;
    width: 20%;
    gap: 20px;
    align-items: center;
}
.menu{
    font-size: 1.5rem;
}
.logo{
    width: 20%;
    display: flex;
}
.burgerNav{

    position: fixed;
    right: 0px;
    top: 0;
text-transform: capitalize;
    bottom: 0;
    background-color: #fff;
    width: 300px;
    display: flex;
    /* scrollbar-width: 1px; */
    /* scrol */
    z-index: 20;
    text-align: start;
    /* overflow-y:scroll; */
    overflow-y: auto;
    /* overflow-x: hidden; */
    flex-direction: column;
    /* justify-content: space-evenly; */
}
.navItems{
    padding: 15px 10px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.081);
    list-style: none;
    font-weight: 600;
}
.navItems a{
text-decoration: none;
}
.closeicon{
    /* position: fixed; */
    /* top: 0; */
    width: 100%;
    display: flex;
    padding-inline: 30px;
    padding-top: 30px;
    justify-content: end;
}
.icon{
    font-weight: 900;
    cursor: pointer;
}

.none{
    display: none;
}
@media(max-width:767px){
    .allModels{
        display: none;
    }
    .logo,.rightNav{
        width: 50%;
    }
}
@media(max-width:540px){
    .model{
        display: none;
    }
    .rightNav{
        justify-content: end;
    }
    

}