.section-head{
    /* padding-top:15vh ; */
    padding-top: 15vh;
    text-align: center;
}
.buttonGroup{
    display: flex;
    /* flex-wrap: wrap; */
    align-items: center;
    gap: 16px;
    width: 50%;
    justify-content: center;
    /* flex-direction: column; */
    /* width: 50%; */
}

.leftBtn{
background-color: rgba(23, 26 , 32, 0.8);
height: 40px;
width: 50%;
border-radius: 25px;
text-transform: uppercase;
font-size: 12px;
color: white;
outline: none;
border: none;
opacity: 0.85;
}
.leftBtn:hover{
    cursor: pointer;
    background-color: #fff;
    color: black;
    opacity: 0.60;
}
.rightBtn{
background-color:white;
height: 40px;
width: 50%;
border-radius: 25px;
color: black;
outline: none;
border: none;
opacity: 0.65;
text-transform: uppercase;
font-size: 12px;
}
.rightBtn:hover{
    background-color: black;
    color: white;
    opacity: 0.60;
    cursor: pointer;
}
.downArrow{
    height:40px ;
    animation: animateDown infinite 1.5s;
}
.bottom{
    width: 100%;
    justify-content:space-between;
    align-items: center;
    display: flex;
    flex-direction: column;
    gap: 30px;

}
.fade{
display: flex;
justify-content: center;
width: 100%;
}
